// Put content single javascript here
import StickySidebar from 'sticky-sidebar';
import '../../scss/single/loader.scss';

if (module.hot) {
  module.hot.accept();
}

jQuery(window).load(() => {
  // eslint-disable-next-line no-unused-vars
  const sticky = new StickySidebar('.blog-aside-right ', {
    setBoxHeight: true,
    activeClass: 'fixed-position',
    container: '.blog-aside-right__inner',
    positionType: 'fixed',
    topSpacing: 25,
    extraTop: 10,
  });

  const podcastWidget = [...document.querySelectorAll('[data-podcast-src]')];
  if (podcastWidget.length) {
    podcastWidget.forEach((podcastItem) => {
      const $podcastItem = $(podcastItem);
      const podcastSrc = podcastItem.getAttribute('data-podcast-src');
      const ajaxurl = `https://api.simplecast.com/sites/search?url=${podcastSrc}`;
      $.post(ajaxurl, { url: podcastSrc }, (result, status) => {
        if ('success' === status && 'undefined' !== typeof result.podcast
          && 'undefined' !== typeof result.podcast.id) {
          const getUrl = `https://api.simplecast.com/podcasts/${result.podcast.id}/distribution_channels`;
          $.get(getUrl, {}, (channelsResult, channelsStatus) => {
            if ('success' === channelsStatus
              && 'object' === typeof channelsResult.collection) {
              window.distribChannels.forEach((channelItem) => {
                channelsResult.collection.forEach((item) => {
                  const distrib = item.distribution_channel;
                  if (channelItem === distrib.name) {
                    const distribHref = item.url;
                    const li = $('<li/>');
                    const anchor = $('<a/>')
                      .attr('target', '_blank')
                      .attr('href', distribHref);
                    $('<img/>')
                      .attr('src', window.distribImages[distrib.name])
                      .appendTo(anchor);
                    anchor.appendTo(li);
                    li.appendTo($podcastItem);
                  }
                });
              });
            }
          });
        }
      });
    });
  }
});
